import * as React from 'react';
import Box from '@mui/material/Box';

import MovieCard from './MovieCard';

function MovieInfo() {
    return (
        <Box sx={{ 
            display: 'flex',
            justifyContent: 'space-around'
        }}>
            <MovieCard name="4" content="" />
            <MovieCard name="2" />
            <MovieCard name="" />
            <MovieCard name="" />
        </Box>
    )
}

export default MovieInfo;